

let Socket = {

    _host: '',
    _hostId: 0,
    _websocket: null,
    _online: false,
    _connected: false,
    _connecting: false,
    _onStatusChange: null,

    _decelerate: null,
    _decelerateTimeout: 0,
    _decelerateMaxTimeout: 30,

    get connected() {
        return this._connected;
    },

    set connected(value) {
        this._connected = value;
        this._statusChange();
    },

    get connecting() {
        return this._connecting;
    },

    set connecting(value) {
        this._connecting = value;
        this._statusChange();
    },


    get online() {
        return this._online;
    },

    set online(value) {
        this._online = value;
        this._statusChange();
    },

    _make: function({ host }) {
        this._host = host;
        this._onceQueue = {};
        this._listeners = {};
        this._onStatusChange = [];

        this.online = navigator.onLine;

        window.addEventListener('online', () => {
            this.online = navigator.onLine;
            console.log('Socket: online');
            this.init();
        });

        window.addEventListener('offline', () => {
            this.online = navigator.onLine;
            console.log('Socket: offline');
            this.reconnect();
        })
    },

    _ack: function(messageId) {
        this.sendMessage({}, `response::${messageId}`);
    },

    _onMessage: function(message) {
        this._ack(message.id);

        if (this._onceQueue[message.type]) {
            this._onceQueue[message.type].forEach(fn => fn(message.data));
            this._onceQueue[message.type] = [];
        }

        if (this._listeners[message.type]) {
            this._listeners.forEach(fn => fn(message.data));
        }
    },

    _statusChange: function() {
        this._onStatusChange.forEach(fn => fn());
    },

    _onceQueue: null,
    _listeners: null,

    sendMessage: function(message, type="message") {
        let data = {
//            id: uuid.v4(null, window.performance.now()),
            data: message,
            type: type,
        };

        let promise = new Promise((success, timeout) => {
            if (type.indexOf('response::') === 0) {
                return success();
            }

            let timeoutId = setTimeout(timeout, 10000);

            this.once(`response::${data.id}`, data => {
                clearTimeout(timeoutId);
                success(data.message);
            });
        });

        promise.catch(() => this.reconnect());

        this.websocket.send(JSON.stringify(data));
    },

    reconnect: function() {
        this.websocket.close();
    },

    init: function() {
        if (this.online && !this.decelerate) {

            this.connecting = true;
            this._decelerate = setTimeout(() => {
                console.log('trying to establishe a connection...');
                this._decelerate = null;
                this.websocket = new WebSocket(`ws://${this._host[this._hostId]}`);

                this.websocket.onopen = () => {
                    this.connected = true;
                    this.connecting = false;
                    this._decelerateTimeout = 0;
                    console.log('connection established!');
                }

                this.websocket.onerror = function(error) {
                    console.error('websocket error:', error);
                }

                this.websocket.onclose = () => {
                    // socket closed restart
                    this._hostId += 1;
                    this._decelerateTimeout += 1;

                    if (this._hostId > this._host.length - 1) {
                        this._hostId = 0;
                    }

                    if (this._decelerateTimeout > this._decelerateMaxTimeout) {
                        this._decelerateTimeout = this._decelerateMaxTimeout;
                    }

                    console.warn(`websocket disconnected! Retry in ${this._decelerateTimeout}s to ${this._host[this._hostId]}`);
                    this.connected = false;
                    this.init();
                }

                this.websocket.onmessage = this._onMessage.bind(this);
            }, this._decelerateTimeout * 1000);
        }
    },

    once: function(type, fn) {
        if (!this._onceQueue[type]) {
            this._onceQueue[type] = [];
        }

        this._onceQueue.push(fn);
    },

    onStatusChange: function(fn) {
        this._onStatusChange.push(fn);
    }
};

export default Socket;



